import React, { Component } from 'react';
import './Todo.css';

class Todo extends Component {
    constructor(props){
        super(props);
        this.state = {
            todoText: props.todoText
        }
    }

    deleteToDo(event){
        //get the values of the fields and put them as params to the line below
        event.preventDefault();
        this.props.deleteToDo(this.props.id);
        console.log("deleted up bitch");
    }

    checkTodo(event){
        event.preventDefault();
        this.props.checkTodo(this.props.id, this.props.completed);
        console.log("checked up bitch");
    }

    render() {
    return (
        <div className = "list-item">
            <input type="checkbox" name = "checkbox" className = {this.props.completed.toString()} value = {this.props.completed} onChange = {event => this.checkTodo(event)}></input>
            <span>{this.state.todoText}</span>
            <button className = "delete-button" onClick={ event => this.deleteToDo(event)}>delete</button>
        </div>
    );
  }
}

export default Todo;
