import React, { Component } from 'react';
import './NewTodo.css';

class NewTodo extends Component {
    constructor(props){
        super(props);
        this.state = {
            // onClick: props.onClick
            textInput: ""
        }
        this.handleChange = this.handleChange.bind(this)
        this.addTodo = this.addTodo.bind(this)
    }
    addTodo(event){
        //get the values of the fields and put them as params to the line below
        event.preventDefault()
        this.props.postToDo(this.state.textInput)
        this.render()
    }
    
    handleChange(event){
        this.setState({textInput: event.target.value})
    }
    
    render() {
    return (
        <div id = "main-container">
            <h1>ToDo List</h1>
            <form id = "input-form">
                <input id = "text-input" type="text" name="ToDo" onChange = {this.handleChange}></input>
                <input id = "add-button" type="submit" value="Add" onClick={this.addTodo}></input>
            </form>
        </div>
    );
  }
}

export default NewTodo;