import React, { Component } from 'react';
import './App.css';
import Todo from './Todo';
import NewTodo from './NewTodo';
// import { render } from 'react-dom';


class App extends Component {
  // CONSTRUCTOR 
  constructor(props){
    super(props);
    this.state = { todos: [], url: "https://cse204.work/todos"}
    this.postToDo = this.postToDo.bind(this);
    this.deleteToDo = this.deleteToDo.bind(this);
    this.checkTodo = this.checkTodo.bind(this);
  };

  // RENDER 
  render() {
    let self = this;
    var xhttp = new XMLHttpRequest();
    xhttp.onreadystatechange = function() {
      if (this.readyState === 4 && this.status === 200) {
        var parsedData = JSON.parse(this.responseText);
        self.setState({
          todos: parsedData
        });
        
      }
      else if (this.readyState === 4) {
        console.log("Some non 200 status. Response text:")
        console.log(this.responseText);
      }
    };
    var url = "https://cse204.work/todos";
    xhttp.open("GET", url, true);
    //xhttp.setRequestHeader("Content-type", "application/json");
    xhttp.setRequestHeader("x-api-key","d7ebf2-ae7d7b-410afc-1a4813-42854c");
    xhttp.send();

    if (self.state.todos.length > 0 ){
      self.state.todos.sort((a, b) =>
      a.text.localeCompare(b.text))
    }

    return (
      <div id = "main-container">
        <NewTodo postToDo={this.postToDo}/>
        
        {this.state.todos.map((todo) =>
        <Todo key={todo.id} id={todo.id} completed={todo.completed}
        todoText={todo.text} deleteToDo={this.deleteToDo} checkTodo={this.checkTodo}/>) }
      </div>
    );
  }

  //DELETE To Do from the API
  deleteToDo(id){
    var xhttp3 = new XMLHttpRequest();
    xhttp3.onreadystatechange = function() {
      if (this.readyState === 4 && this.status === 200) {
      }
      else if (this.readyState === 4) {
        console.log(this.responseText);
      }
  };
  var url = "https://cse204.work/todos";
  xhttp3.open("DELETE", url+"/"+id, true);
  xhttp3.setRequestHeader("x-api-key","d7ebf2-ae7d7b-410afc-1a4813-42854c");
  xhttp3.send();
}

  // POST TODOS 
  postToDo(input){
    var xhttp2 = new XMLHttpRequest();
    xhttp2.onreadystatechange = function() {
      if (this.readyState === 4 && this.status === 200) {
        // this.componentDidMount();
        console.log("hello pooperz");
        document.getElementById("text-input").value = "";
      }
      else if (this.readyState === 4) {
        console.log(this.responseText);
      }
    };
    var url = "https://cse204.work/todos";
    xhttp2.open("POST", url, true);
    xhttp2.setRequestHeader("Content-type", "application/json");
    xhttp2.setRequestHeader("x-api-key","d7ebf2-ae7d7b-410afc-1a4813-42854c");
    xhttp2.send(JSON.stringify({text: input, completed: false}));
  }

  //PUT an update on the To Do
  //CHECKING WORKS -- it sends to the API it just doesn't update visually
  checkTodo(id, completed){
    var xhttp4 = new XMLHttpRequest();
    xhttp4.onreadystatechange = function() {
      if (this.readyState === 4 && this.status === 200) {
        // document.getElementById(id).classList.add("checked");
      }
      else if (this.readyState === 4) {
        console.log(this.responseText);
      }
    };
    var url = "https://cse204.work/todos";
    xhttp4.open("PUT", url+"/"+id, true);
    xhttp4.setRequestHeader("Content-type", "application/json");
    xhttp4.setRequestHeader("x-api-key","d7ebf2-ae7d7b-410afc-1a4813-42854c");
    xhttp4.send(JSON.stringify({completed: completed}));
  }

}

export default App;
